import { Streamlit, RenderData } from "streamlit-component-lib"

function onRender(event: Event): void {
  const data = (event as CustomEvent<RenderData>).detail

  // Remove existing content
  let child = document.body.lastElementChild;
  if (child) {
    document.body.removeChild(child)
  }

  // eslint-disable-next-line
  const resizeObserver = new ResizeObserver(entries => {
    Streamlit.setFrameHeight()
  })

  // Add the HTML content
  let div = document.body.appendChild(document.createElement("div"))
  div.innerHTML = data.args["html_content"]

  // Add click detection on each link
  let links = document.getElementsByTagName("a");
  for (let i = 0; i < links.length; i++) {
    links[i].onclick = function (): void {
      Streamlit.setComponentValue(links[i].id)
    }
  }

  // Make sure that the frame height is adjusted when images are loaded
  let images = document.getElementsByTagName("img");
  for (let i = 0; i < images.length; i++) {
    images[i].onload = function (): void {
      Streamlit.setFrameHeight()
    }
  }
  Streamlit.setFrameHeight()
}

Streamlit.events.addEventListener(Streamlit.RENDER_EVENT, onRender)
Streamlit.setComponentReady()